import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobals
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.silk.defer.DeferringHost
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main() {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { io.github.jdgarita.pages.HomePage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerStyle("headline-text", io.github.jdgarita.HeadlineTextStyle)
        ctx.theme.registerStyle("subheadline-text", io.github.jdgarita.SubheadlineTextStyle)
        ctx.theme.registerStyle("about-image", io.github.jdgarita.styles.AboutImageStyle)
        ctx.theme.registerStyle("about-text", io.github.jdgarita.styles.AboutTextStyle)
        ctx.theme.registerStyle("back-to-top-button",
                io.github.jdgarita.styles.BackToTopButtonStyle)
        ctx.theme.registerStyle("navigation-item", io.github.jdgarita.styles.NavigationItemStyle)
        ctx.theme.registerStyle("mini-me", io.github.jdgarita.styles.MiniMeStyle)
        ctx.theme.registerStyle("social-link", io.github.jdgarita.styles.SocialLinkStyle)
        ctx.theme.registerStyle("main-button", io.github.jdgarita.styles.MainButtonStyle)
        ctx.theme.registerStyle("main-image", io.github.jdgarita.styles.MainImageStyle)
        ctx.theme.registerStyle("qualification-card",
                io.github.jdgarita.styles.QualificationCardStyle)
        ctx.theme.registerVariant("-circle", io.github.jdgarita.CircleButtonVariant
        )
        ctx.theme.registerVariant("-uncolored", io.github.jdgarita.UncoloredButtonVariant
        )
        io.github.jdgarita.initSiteStyles(ctx)
        io.github.jdgarita.initTheme(ctx)
    }

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    router.navigateTo(window.location.href.removePrefix(window.location.origin),
            UpdateHistoryMode.REPLACE)

    val root = document.getElementById("root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    AppGlobals.initialize(mapOf("title" to "JD Garita"))
    renderComposable(rootElementId = "root") {
        io.github.jdgarita.AppEntry {
            router.renderActivePage { DeferringHost { it() } }
        }
    }
}
